import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-welcome',
  templateUrl: './welcome.component.html',
  styleUrls: ['./welcome.component.css']
})
export class WelcomeComponent implements OnInit {

  path = '../../assets/images/galleryImages/i5.JPG';
  paths: string [] = [
    '../../assets/images/galleryImages/i2.JPG',
    '../../assets/images/galleryImages/i4.JPG',
    '../../assets/images/galleryImages/i11.JPG',
    '../../assets/images/galleryImages/i15.JPG',
    '../../assets/images/galleryImages/i18.JPG',
    '../../assets/images/galleryImages/i5.JPG'
  ];
  i = 0;

  ngOnInit(): void {
    setInterval(this.image.bind(this), 3000);
  }

  image() {
    this.path = this.paths[(this.i++) % 6];
  }

}



