import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { MaterialComponent } from './material/material.component';
import { NavComponent } from './nav/nav.component';
import {ScrollToModule} from 'ng2-scroll-to';
import { GalleryComponent } from './gallery/gallery.component';
import { ContactComponent } from './contact/contact.component';
import { AboutComponent } from './material/about/about.component';
import { TeamComponent } from './material/team/team.component';
import { WelcomeComponent } from './welcome/welcome.component';
import {RouterModule, Routes} from '@angular/router';
import { MainGalleryComponent } from './main-gallery/main-gallery.component';
import {Angular2ImageGalleryModule} from 'angular2-image-gallery';
import { AgmCoreModule } from '@agm/core';
const appRoutes: Routes = [
  {path: 'index', component: MaterialComponent},
  {path: 'gallery', component: MainGalleryComponent},
  { path: '',
    redirectTo: '/index',
    pathMatch: 'full'
  },
  { path: '**', redirectTo: '/index' }
];

@NgModule({
  declarations: [
    AppComponent,
    MaterialComponent,
    NavComponent,
    GalleryComponent,
    ContactComponent,
    AboutComponent,
    TeamComponent,
    WelcomeComponent,
    MainGalleryComponent
  ],
  imports: [
    BrowserModule,
    ScrollToModule,
    RouterModule.forRoot(appRoutes,{useHash: true}),
    Angular2ImageGalleryModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyA5FWT6klM9U5WLCiiwIONMtRtOL8pazNE'
    })
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



